.input {
  width: 100%;
  padding: 0.6rem 0.75rem;
  border-radius: 10px;
  border: 1px solid rgba(15,23,42,0.06);
  background: #fff;
  outline: none;
  box-shadow: none;
}
.input:focus {
  border-color: rgba(15,23,42,0.12);
  box-shadow: 0 6px 20px rgba(2,6,23,0.04);
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.6rem 1rem;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.btn--primary {
  background: var(--accent);
  color: #fff;
}
