@keyframes slideInFromTop {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideOutToTop {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-20px);
  }
}
.animate-slide-in {
  animation: slideInFromTop 0.3s ease-out;
}
.animate-slide-out {
  animation: slideOutToTop 0.3s ease-out;
